.sidebar {
  flex: 0 0 18.5rem;
}

.sidebar__menu {
  padding: 2rem;
  margin-left: -2rem;
}

.sidebar__item {
  padding-left: 2rem;
  position: relative;
}

.sidebar__item span {
  vertical-align: bottom;
}

.sidebar__item:hover a:any-link, .sidebar__item.active a:any-link {
  color: #198754;
}

.sidebar__item:hover::before, .sidebar__item.active::before {
  content: '';
  position: absolute;
  left: 0;
  width: 0.5rem;
  height: 2rem;
  border-left: 4px solid #198754;
}

.sidebar__author-img {
  background: url("../../assets/images/andychen.jpeg") center/cover;
  min-width: 2.5rem;
  height: 2.5rem;
}

.header__item a:hover:any-link {
  color: #198754;
}

.header__item a:hover {
  transform: scale(1.5);
}

.header .adminPlus {
  cursor: pointer;
}

.header .adminPlus:hover:any-link {
  color: #198754;
}

.header .adminPlus:hover {
  transform: scale(1.5);
}

.qaPart__collection {
  max-width: 20rem;
  width: 100%;
}

.qaPart__details {
  max-width: 36.5rem;
  width: 100%;
}

.qaPart__caption a:hover:any-link {
  color: #198754;
}

.qaPart__caption a:hover {
  transform: scale(1.5);
}

.qaPart__icons a:hover:any-link {
  color: #198754;
}

.qaPart__icons a:hover {
  transform: scale(1.5);
}

.qaPart__student-imgS, .qaPart__student-imgT, .qaPart__teacher-imgS, .qaPart__teacher-imgT {
  min-width: 2.5rem;
  height: 2.5rem;
}

.qaPart__student-content, .qaPart__teacher-content {
  font-size: 0.875rem;
}

.qaPart__student-imgS {
  background: url("../../assets/images/emilywang.jpeg") center/cover;
}

.qaPart__student-file:hover {
  box-shadow: 2px 4px 8px #6C757D;
}

.qaPart__teacher-imgT {
  background: url("../../assets/images/andychen.jpeg") center/cover;
}

.qaPart__teacher-comment li {
  text-indent: 1rem;
}

.qaPart__replyEdit {
  height: 3.5rem;
  line-height: 3.5rem;
}

.qaPart__replyEdit-style li a:hover:any-link {
  color: #198754;
}

.qaPart__replyEdit-style li a:hover {
  transform: scale(1.5);
}

.qaPart__replyEdit-content {
  height: 16.4375rem;
}

.next__arrows li a:hover:any-link {
  color: #198754;
}

.next__arrows li a:hover {
  transform: scale(1.5);
}

.admin .text-end a:hover:any-link {
  color: #198754;
}

.admin .text-end a:hover {
  transform: scale(1.1);
}

.admin .table {
  margin-bottom: 3.9375rem;
}

.admin__modalView-img {
  max-width: 8.8125rem;
  height: 8.8125rem;
}

.page {
  margin-bottom: 2rem;
}

.page span {
  font-size: 1rem;
}
